.buttons{
  
   height: 6vh;
   text-align: center;
   justify-content: space-between;
}
.email{
	border: none;
	font-size: 2vh;
	width: 15vh;
	color: black;
	background-color: whitesmoke;
	line-height: 5vh;
	padding: 0vh 2.0vh 0vh 2.0vh;
	border-radius: 9px;
    margin: 0 2vh 0 0 ;
}
.email i{
	padding-right: 1vh;
}
.linkedin{
	border: none;
	font-size: 2vh;
	width: 15vh;
	line-height: 5vh;
	padding: 0vh 1.0vh 0vh 1.0vh;
	color: whitesmoke;
	background-color: #5093E2;
	border-radius: 9px;
}
.linkedin i{
	padding-right: 1vh;
}
